.modal {
    width: 280px;
    position: absolute;
    top: 500px;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #FFFFFF;
    border: 2px solid $primary-yellow;
    border-radius: 10px;
@media screen and (max-width: 1199px) {
        overflow-y: scroll;
    }
@media screen and (min-width: $tablet) {
    width: 568px;
    top: 350px;
}
}

.modal__title {
    font-family: 'Seymour One';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.5;
    color: #000;
    padding-top: 28px;
    text-align: center;
    @media screen and (min-width: $tablet) {
            padding-top: 32px;
            font-size: 20px;
            line-height: 1.4;
}
}
.modal-image{
    display: flex;
    justify-content: center;
    padding-top: 83px;
    padding-bottom: 24px;
}
@media screen and (min-width: $tablet) {
    .modal-image {
            padding-top: 103px;
            padding-bottom: 40px;
    } 
    .modal-cards{
        display: flex;
        flex-wrap: wrap;
        gap: 32px;
    }
}
@media screen and (max-width: 767px) {
    .modal-cards__item:not(:first-child) {
        margin-top: 83px;
    }
}
.modal-cards__link{
    display: flex;
        align-items: flex-end;
        justify-content: center;
        width: 130px;
        height: 135px;
        padding-bottom: 20px;
        filter: drop-shadow(0px 5.26027px 19.726px rgba(212, 20, 67, 0.1));
        border-radius: 15.7808px;
}
